<template>
  <div id="app" class="content" ref="addImage">
    <div class="onFlex">
      <div ref="capture" class="image-content">
        <img
          class="imgTitle"
          src="./img/详细体检项目.jpg"
          alt="">
        <div class="TJXM">
          <p class="TJXM_XM">项目</p>
          <p class="TJXM_ZB">项目内容</p>
          <p v-if="deleteTJXM" class="TJXM_CZ">操作</p>
        </div>
        <div class="TJXMContent" v-bind:class="{ifBGColor:index%2===1}" v-for="(item,index) in TJXM">
          <p class="TJXMContent_XM">{{item.项目}}</p>
          <div class="line"></div>
          <p class="TJXMContent_ZB">{{item.指标}}</p>
          <button class="TJXMContent_CZ" v-if="deleteTJXM" v-bind:index="index" @click="removeTJXM(index)">删除</button>
        </div>
      </div>
      <div class="inputBoxAll">
        <div class="inputBox">
          <h3 class="inputBox_title">详细体检项目</h3>
          <p>
            项&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：
            <input type="text" v-model="xiangmu" v-bind:name="'项目'">
          </p>
          <p>
            项目内容：
            <textarea type="text" v-model="zhibiao" v-bind:name="'指标'" rows="7"/>
          </p>
          <button @click="addTJXMBtn">加入</button>
        </div>
        <div
          @click="generatorImage"
          class="img-btn"
          style="width: 1rem;
          height: .5rem;
          font-size: .2rem;
          text-align: center;
          line-height:.5rem;
          background: blue;
          color: #FFFFFF;
          border-radius: .2rem;
          cursor: pointer;
          margin-top: .1rem">
          生成图片
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import html2canvas from 'html2canvas';

  export default {
    name: 'App',
    data() {
      return {
        deleteTJXM: true,
        TJXM: [],
        xiangmu: '',//项目
        zhibiao: ''//指标
      }
    },
    methods: {
      generatorImage() {
        let that = this;

        function onPromise() {
          return new Promise((resolve, reject) => {
            that.deleteTJXM = false;
            window.scrollTo(0, 0);
            resolve();
          }).then(() => {
            html2canvas(that.$refs.capture).then(canvas => {
              that.$refs.addImage.append(canvas);
              let link = document.createElement('a');
              link.href = canvas.toDataURL();
              link.setAttribute('download', '图片canvas.png');
              link.style.display = 'none';
              document.body.appendChild(link);
              link.click();
            })
          }).then(() => {
            that.deleteTJXM = true;
          })
        }

        onPromise();
      },
      addTJXMBtn() {
        let that = this;
        console.log(that.xiangmu);
        console.log(that.zhibiao);
        if (that.xiangmu && that.zhibiao) {
          let data = that.TJXM;
          data.push({
            项目: that.xiangmu,
            指标: that.zhibiao,
            功能建议: that.yijian
          });
          that.TJXM = data;
          that.xiangmu = "";
          that.zhibiao = ""
        } else {
          alert('输入框不能为空！！')
        }
      },
      removeTJXM(index) {
        let that = this;
        let data = [];
        that.TJXM.map((item, i) => {
          if (i !== index) {
            data.push(item);
          }
        });
        that.TJXM = data;
      }
    }
  };
</script>

<style>
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  .ifBGColor {
    background: #f0f5fb;
  }

  .onFlex {
    display: flex;
  }

  .image-content {
    width: 7.5rem;
  }

  .imgTitle {
    width: 7.5rem;
    margin-top: .88rem;
  }

  .TJXM {
    width: 7.1rem;
    margin: 0 auto;
    display: flex;
    background: #66aaff;
    border-top-left-radius: .16rem;
    border-top-right-radius: .16rem;
  }

  .TJXM_XM {
    width: 2.14rem;
    font-size: .28rem;
    color: #FFF;
    text-align: center;
    border-right: .01rem solid #FFF;
    padding: .14rem 0 .19rem 0;
    font-weight: 400;
  }

  .TJXM_ZB {
    width: 4.96rem;
    font-size: .28rem;
    color: #FFF;
    text-align: center;
    border-right: .01rem solid #FFF;
    padding: .14rem 0 .19rem 0;
    font-weight: 400;
  }

  .TJXM_CZ {
    width: .7rem;
    font-size: .2rem;
    color: #FFF;
    padding: .14rem 0 .19rem 0;
    text-align: center;
  }

  .TJXMContent {
    width: 7.1rem;
    margin: 0 auto;
    display: flex;
  }

  .TJXMContent_XM {
    width: 2.12rem;
    font-size: .26rem;
    color: #66aaff;
    font-weight: 400;
    padding: .14rem 0 .19rem 0;
    border-bottom: .01rem solid #dbe6f3;
  }

  .line {
    width: 0;
    border-left: .01rem solid #dbe6f3;
  }

  .TJXMContent_ZB {
    width: 4.96rem;
    font-size: .24rem;
    color: #666;
    padding: .14rem .2rem .19rem .2rem;
    border-bottom: .01rem solid #dbe6f3;
  }

  .TJXMContent_XM,
  .TJXMContent_ZB {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
  }

  .TJXMContent_XM {
    justify-content: flex-start;
    padding-left: .1rem;
  }

  .TJXMContent_CZ {
    width: .7rem;
    font-size: .1rem;
  }

  .inputBoxAll {
    position: fixed;
    left: 7.3rem;
    top: 1rem;
    margin-top: 1rem;
    margin-left: .2rem;
  }

  .inputBox:nth-child(2) {
    margin-top: .2rem;
  }

  .inputBox_title {
    font-size: .14rem;
    text-align: center;
  }

  .inputBox > p {
    margin-top: .1rem;
  }

  .inputBox input,
  .inputBox textarea{
    outline: none;
  }

  .inputBox > button {
    margin-top: .1rem;
    width: 100%;
    font-size: .14rem;
    padding: .05rem;
    border-radius: .08rem;
    outline: none;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    font-size: .07rem;
  }
</style>



